import React, { Component } from 'react';
import styled from 'styled-components';
import { CHOICES } from '../../constants';

/**
 * Look at ゴーシャO and U, pretty cool font. Or Gosha OS
 */
const Container = styled.div`
  width: calc(100% - 72px);
  height: auto;
  padding: 8px 36px;
  background: ${props => props.theme.base.background};
  display: flex;
  flex-direction: row;
  text-align: center;
`;

const ContainerItem = styled.div`
  order: ${props => props.flexOrder};
  flex: ${props => props.flexGrow};
`;

/*
 * NOTE: This component should be the one outputing the different kanji containers.
 */
class ChangeLevel extends Component {
  constructor(props) {
    super(props);
  }

  onArrowClick = (left = true) => {
    const { currentLevel, onKanjiLevelChange } = this.props;
    const newLevelKey = left ? currentLevel.prev : currentLevel.next;
    onKanjiLevelChange(CHOICES[newLevelKey]);
  }

  renderArrow = (left = true) => {
    let symbol = left ? '←' : '→';
    return (
      <ContainerItem
        flexGrow={1}
        onClick={() => this.onArrowClick(left)}
      >
        {symbol}
      </ContainerItem>
    );
  }

  render() {
    const { currentLevel } = this.props;
    const { prev, next } = currentLevel;
    return (
      <Container>
        {prev ? this.renderArrow() : null}
        <ContainerItem flexGrow={2}>
          {currentLevel.label}
        </ContainerItem>
        {next ? this.renderArrow(false) : null}
      </Container>
    );
  }
}

export default ChangeLevel;
