import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import ModalBase from './Modal';
import { popModal, tapSeen, tapMastered } from '../../actions';

export const KanjiContentContainer = styled.div`
  height: auto;
`;

export const KanjiRow = styled.div`
  width: 100%;
  height: auto;
  text-align: center;
`;

export const KanjiDisplay = styled.div`
  padding: 36px 0;
  font-size: 80px;
  font-weight: bold;
  margin: 0 auto;
`;

export const KanjiOptionRow = styled.div`
  width: calc(100% - 48px);
  padding: 24px;
  display: flex;
  text-align: center;
`;

export const OptionRow = styled.div`
  width: 100%;
  height: 50px;
  text-align: center;
  font-size: 24px;
  flex: 1;

  .modalmoji {
  }

  .modal-value {
    font-weight: 600;
    padding-left: 24px;
    color: hsl(0, 0%, 45%);
  }
`;

export const OptionContent = styled.div`
  padding: 13px 0;
`;

class KanjiModal extends Component {
  constructor(props) {
    super(props);
  }

  static propTypes = {
    kanji: PropTypes.string,
    /* NOTE: this id is a number, but got casted of as a string */
    id: PropTypes.string,

    /* FUTURE PROPS */
    seen: PropTypes.bool,
    mastered: PropTypes.bool,
    /* FUTURE PROPS */
  };

  /**
   * Clicking the more info button replaces the OptionContents with more information
   * 1) Fetch information
   * 2) Displays them
   *
   * TODO: Need to display more information under kanji
   * <button /> - IE6 problems, implicitly submits
   */
  tapAndExitModal = optionCallback => value => {
    optionCallback(value);
    this.props.popModal();
  }

  renderSeen = () => {
    const tapSeenAndExit = this.tapAndExitModal(this.props.tapSeen);
    return (
      <OptionRow>
        <OptionContent>
          <span
            className='modalmoji'
            onClick={() => tapSeenAndExit(this.props.id)}>
            👀
          </span>
        </OptionContent>
      </OptionRow>
    );
  }

  renderMastered = () => {
    const tapMasteredAndExit = this.tapAndExitModal(this.props.tapMastered);
    return (
      <OptionRow>
        <OptionContent>
          <span
            className='modalmoji'
            onClick={() => tapMasteredAndExit(this.props.id)}>
            🤓
          </span>
        </OptionContent>
      </OptionRow>
    );
  }

  renderInfo = () => {
    return (
      <OptionRow>
        <OptionContent>
          <span className='modalmoji'>💁🏽‍♂️</span>
        </OptionContent>
      </OptionRow>
    );
  }

  render() {
    return (
      <ModalBase>
        <KanjiContentContainer>
          <KanjiRow>
            <KanjiDisplay>
              {this.props.kanji}
            </KanjiDisplay>
          </KanjiRow>
          <KanjiOptionRow>
            {this.renderSeen()}
            {this.renderMastered()}
            {this.renderInfo()}
          </KanjiOptionRow>
        </KanjiContentContainer>
      </ModalBase>
    );
  }
}

const mapStateToProps = () => ({});
const mapDispatchToProps = dispatch => ({
  tapSeen: kanjiId => dispatch(tapSeen(kanjiId)),
  tapMastered: kanjiId => dispatch(tapMastered(kanjiId)),
  popModal: () => dispatch(popModal()),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(KanjiModal);
