import React, { Component } from 'react';
import { createStore } from 'redux';
import { Provider } from 'react-redux';
import { ThemeProvider } from 'styled-components';
import mainReducer from './reducers';
import './css-reset.css';
import './index.css';
import AppContainer from './components';
import themes from './theme';
import KanjiMasteryManager from './utils/KanjiMasteryManager';

const store = createStore(mainReducer, KanjiMasteryManager.getMasteryData());
// Persist state to localStorage, in the dispatch we can also make ajax call + redux thunk
store.subscribe(() => {
  const { kanjiMastery, lastAction } = store.getState();
  KanjiMasteryManager.updateMasteryData(lastAction, kanjiMastery);
});

/*
 * Identify all the things that is colored
 */
class App extends Component {
  render() {
    return (
      <Provider store={store}>
        <ThemeProvider theme={themes}>
          <AppContainer />
        </ThemeProvider>
      </Provider>
    );
  }
}

export default App;
