import {
  TAP_SEEN,
  TAP_MASTERED,
} from '../actions';
import { SEEN, MASTERED } from '../constants';

const kanjiMasteryDefaultState = {
  /**
   * mastery = {
   *  id1: SEEN,
   *  id2: MASTERED,
   *  ...
   * }
   */
  mastery: {},
};

/**
 * Return the correct branch
 */
export default (state = kanjiMasteryDefaultState, action) => {
  if (typeof state === 'undefined') return kanjiMasteryDefaultState;
  let { mastery } = state;
  switch (action.type) {
    case TAP_SEEN:
      mastery = Object.assign({}, mastery, { [action.kanjiId]: SEEN });
      break;
    case TAP_MASTERED:
      mastery = Object.assign({}, mastery, { [action.kanjiId]: MASTERED });
      break;
    default:
      break;
  }
  return { mastery };
};
