import { TAP_SEEN, TAP_MASTERED } from '../actions';
import StoreManager from './StoreManager';
import { LOCAL_STORAGE_CACHE_KEY } from '../constants';

const MASTERY_ACTIONS = [TAP_SEEN, TAP_MASTERED];
const LocalStorage = StoreManager(window && window.localStorage);

/**
 * Very simple utility functions to get kanji mastery data.
 */
const updateMasteryData = (lastAction, kanjiMastery) => {
  const { type } = lastAction;
  if (!MASTERY_ACTIONS.includes(type)) return;
  LocalStorage.setItem(LOCAL_STORAGE_CACHE_KEY, JSON.stringify(kanjiMastery.mastery));
};

const getMasteryData = () => {
  try {
    const cachedData = LocalStorage.getItem(LOCAL_STORAGE_CACHE_KEY);
    const parsedData = JSON.parse(cachedData);
    return { kanjiMastery: {
      mastery: parsedData,
    }};
  } catch (e) {
    return {};
  }
};

export default {
  getMasteryData,
  updateMasteryData,
};
